
import axios from 'axios';
import ArticleTitleAtomVue from '../atoms/ArticleTitleAtom.vue';
import CommentMoleculesVue from '../molecules/CommentMolecules.vue';
import DisplayTimeAtomVue from '../atoms/DisplayTimeAtom.vue';
import RouterLinkAtomVue from '../atoms/RouterLinkAtom.vue';
import { defineComponent } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';

interface Thread {
  id: number,
  user_id: number,
  user_name: string,
  thread_title: string,
  thread_description: string,
  tag_id: number,
  tag: string,
  comments: Comment[],
  created_at: Date,
  updated_at: Date,
  bold: boolean|number,
  italic: boolean|number,
  underline: boolean|number
}

interface Comment {
  id: number,
  user_id: number,
  user_name: string,
  index: number,
  thread_id: number,
  thread_title: string,
  comment: string,
  bold: string|boolean,
  italic: string|boolean,
  underline: string|boolean,
  src: string
  created_at: Date,
  updated_at: Date
}


export default defineComponent({
    name: 'ThreadArticle',
    components: {
        ArticleTitleAtomVue,
        CommentMoleculesVue,
        DisplayTimeAtomVue,
        RouterLinkAtomVue
    },
    props: {
        thread: {
            type: Object as () => Thread|null|string
        },
        comment: {
            type: Object as () => Comment
        }
    },
    setup() {
        const store = useStore();
        const router = useRouter();
        const route = useRoute();

        const description = (msg: string):string => {
            const count = msg.length;
            if(count >= 95) {
                return msg.substring(0, 95) + '...';
            } else {
                return msg
            }
        };

        const getComment = (data: Comment[]): Comment[] => {
            if(data.length > 2) {
                return data.slice(data.length - 3, data.length)
            } else {
                return data;
            }
        };

        const deleteThread = (id: number): void => {
            axios.delete('/api/delete/' + id, {
                params: {
                    id: id,
                    user_id: 1,
                }
            })
            .then(() => {
                router.push('/');
                store.dispatch('getThreads');
                store.dispatch('getTags', {id: 0, path: route.name});
            })
        };

        return {
            store,
            route,
            description,
            getComment,
            deleteThread
        }
    }
});
